import { clamp, round } from "@zindex/canvas-engine";
import { Color, Path, Point } from "@zindex/canvas-engine";
export function interpolateNumber(from, to, percent = 0.5) {
    return from + percent * (to - from);
}
export function interpolatePositiveNumber(from, to, percent = 0.5) {
    return Math.max(0, from + percent * (to - from));
}
export function interpolatePercent(from, to, percent = 0.5) {
    return clamp(interpolateNumber(from, to, percent), 0, 1);
}
export function interpolateColorComponent(from, to, percent = 0.5) {
    return clamp(Math.round(from + percent * (to - from)), 0, 255);
}
export function interpolateAlphaComponent(from, to, percent = 0.5) {
    if (from == null) {
        from = 1;
    }
    if (to == null) {
        to = 1;
    }
    return clamp(round(from + percent * (to - from)), 0, 1);
}
export function interpolateDiscrete(from, to, percent = 0.5) {
    return percent < 0.5 ? from : to;
}
export function interpolatePoint(from, to, percent = 0.5) {
    return new Point(interpolateNumber(from.x, to.x, percent), interpolateNumber(from.y, to.y, percent));
}
export function interpolatePoly(from, to, percent = 0.5) {
    if (from.length !== to.length) {
        return interpolateDiscrete(from, to, percent).slice();
    }
    const list = [];
    const length = from.length;
    for (let i = 0; i < length; i++) {
        list.push(interpolatePoint(from[i], to[i], percent));
    }
    return list;
}
export function interpolateColor(from, to, percent = 0.5) {
    if (percent <= 0) {
        return from;
    }
    if (percent >= 1) {
        return to;
    }
    return new Color(interpolateColorComponent(from.r, to.r, percent), interpolateColorComponent(from.g, to.g, percent), interpolateColorComponent(from.b, to.b, percent), interpolateAlphaComponent(from.a, to.a, percent));
}
export function interpolateBrush(from, to, percent = 0.5) {
    // TODO: implement different brush interpolation
    return interpolateDiscrete(from, to, percent).clone();
}
export function interpolateDashArray(from, to, percent = 0.5) {
    // TODO:
    return percent < 0.5 ? from : to;
}
export function interpolateMotion(from, to, percent = 0.5) {
    // TODO:
    // @ts-ignore
    return interpolatePoint(from, to, percent);
}
export function interpolateRectRadius(from, to, percent = 0.5) {
    // TODO:
    return percent < 0.5 ? from : to;
}
export function interpolatePathNode(from, to, percent = 0.5) {
    // TODO:
    return interpolateDiscrete(from, to, percent).clone();
}
export function interpolatePath(from, to, percent = 0.5) {
    if (from.nodes.length !== to.nodes.length) {
        return interpolateDiscrete(from, to, percent).clone();
    }
    const length = from.nodes.length;
    const nodes = [];
    for (let i = 0; i < length; i++) {
        nodes.push(interpolatePathNode(from.nodes[i], to.nodes[i], percent));
    }
    // TODO: check if something like mirror, etc.
    return new Path(nodes);
}
