export function clampStep(value, min, max, step = 1) {
    if (isNaN(value) || value <= min) {
        return min;
    }
    if (value >= max) {
        return max;
    }
    if (step === 1) {
        value = Math.round(value);
    }
    else if (!Number.isInteger(value / step)) {
        value = Math.round(value * 1000000000) / 1000000000;
        value -= value % step;
    }
    return value;
}
